import { Injectable } from '@angular/core';
import { Location } from './location.interface';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class LocationService {
  private http: HttpClient
  constructor(http: HttpClient) { 
    this.http = http
  }
  
  fetch(callback: Function) : void {
    this.http.get<Location>('http://localhost:8080/api/locations/').subscribe(jsonObject => {
      callback(jsonObject)  
    });
  }

  post(callback: Function, locationObject: Object) {
    let url = 'http://localhost:8080/api/locations/'
    this.http.post(url, locationObject).subscribe(result => {
      callback(result)
    })
  }

  delete(callback: Function, id: number) : void {
  
    let url = 'http://localhost:8080/api/locations/' + id
    this.http.delete(url, { observe: 'response', responseType: 'text' }).subscribe(response => {
      callback(response.status)
    });
  }

}




