import { Component, OnInit } from '@angular/core';
import { Location } from './location.interface'
import { LocationService } from './location.service';

@Component({
  selector: 'app-locations',
  templateUrl: './locations.component.html',
  styleUrls: ['./locations.component.css']
})
export class LocationsComponent implements OnInit {
  locations: Location[]
  locationService: LocationService

  zoomIntoLocation : Location = {id: 1, latitude: 60, longitude: 60}
  latitude: string
  longitude: string

  // constructor vs init?
  constructor(locationService : LocationService) {
    this.locationService = locationService
  }

  addCoordinates(event) {
    console.log(event)
    this.latitude = event.coords.lat
    this.longitude = event.coords.lng
    this.add()
  }
  add() {
    let object = {"latitude": this.latitude, "longitude": this.longitude}
    // TODO IF WRONG
    // VALIDATION!!
    this.locationService.post((result) => {
      this.locations.push(result)
      this.latitude = ''
      this.longitude = ''

      this.zoomIntoLocation = result
    }, object)

  }
  private deleteLocInCache(id: number) {
    let indexToBeRemoved
    let flag = true
    for(let i=0; i<this.locations.length && flag; i++) {
      if(this.locations[i].id == id) {
        indexToBeRemoved = i
        flag = false
      }
    }      
    this.locations.splice(indexToBeRemoved, 1);    
  }
  delete(id: number) {
    this.locationService.delete((status) => {
      if(status != 200) {
        alert("problem deleting")
      } else {
        this.deleteLocInCache(id)
      }
    }, id);
  }

  
  ngOnInit() {
    this.locationService.fetch((result) => {
        this.locations = result
        this.zoomIntoLocation = this.locations[this.locations.length-1]
    })
  }

}



