import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'firstcharuppercase'})
export class FirstCharUpperCasePipe implements PipeTransform {
  transform(giventext: string): string {
    let firstChar = giventext.charAt(0).toUpperCase()
    let rest = giventext.slice(1, giventext.length).toLowerCase()
    let returnValue : string = firstChar + rest
    return returnValue
  }
}



